import React, { useEffect, useState } from 'react'
import Layout from './../../components/layout/layout';
import AdminMenu from '../../components/layout/adminMenu';
import toast from 'react-hot-toast'
import axios from 'axios'
import CategoryForm from '../../components/form/categoryForm'
import { Modal } from 'antd';
const CreateCategory = () => {
    const [categories, setCategories] = useState([]);
    const [name, setName] = useState("")
    const [visible, setVisible] = useState(false);
    const [updateName, setUpdatedName] = useState("");
    const [selected, setSelected] = useState(null);

    const handleSubmit = async (e) => {
        e.preventDefault();
        try {
            const { data } = await axios.post('/api/v1/category/create-category', { name });
            if (data?.success) {
                toast.success(`${name} is created`);
                getAllCategory();
            }
            else {
                toast.error(data.message);
            }
        } catch (error) {
            console.log(error);
            toast.error("somethings wrong while getting inout");
        }
    }
    // get all category
    const getAllCategory = async () => {
        try {
            const { data } = await axios.get("/api/v1/category/get-category");
            if (data?.success) {
                setCategories(data?.category);
            }
        } catch (error) {
            console.log(error);
            // toast.error({ error });
        }
    };

    useEffect(() => {
        getAllCategory();
    }, []);

    const handleUpdate = async (e) => {
        e.preventDefault();
        try {
            const { data } = await axios.put(`/api/v1/category/update-category/${selected._id}`, { name: updateName })
            if (data.success) {
                toast.success("Category name updated successfully");
                setSelected(null);
                setUpdatedName("");
                setVisible(false);
                getAllCategory();
            }
            else {
                toast.error(data.message)

            }
        } catch (error) {
            toast.error("something went wrong")
        }
    }
    const handleDelete = async (id) => {

        try {
            const { data } = await axios.delete(`/api/v1/category/delete-category/${id}`)
            if (data.success) {
                toast.success("Category deleted successfully");

                setUpdatedName("");

                getAllCategory();
            }
            else {
                toast.error(data.message)

            }
        } catch (error) {
            toast.error("something went wrong while deleted")
        }
    }
    console.log(categories);
    return (
        <Layout title={"admin-create-category"}>
            <div className='container-fluid m-3 p-3'>
                <div className='row'>
                    <div className='col-md-3'>
                        <AdminMenu />
                    </div>
                    <div className='col-md-9'>
                        <h1>Manage Category</h1>
                        <div className='p-3 w-50'>
                            <CategoryForm
                                handleSubmit={handleSubmit}
                                value={name}
                                setValue={setName}
                            />
                        </div>
                        <div className='w-75'>
                            <table className="table  ">
                                <thead>
                                    <tr>
                                        <th scope="col">Name</th>
                                        <th scope="col">Actions</th>
                                    </tr>
                                </thead>
                                <tbody>
                                    {categories?.map((c) => (

                                        <>
                                            <tr>
                                                <td key={c._id}>{c.name}</td>

                                                <td><button className='btn btn-primary ms-2' onClick={() => { setVisible(true); setUpdatedName(c.name); setSelected(c); }} >edit</button>
                                                    <button className='btn btn-danger ms-2' onClick={() => { handleDelete(c._id) }}>delete</button></td>
                                            </tr>
                                        </>
                                    ))}


                                </tbody>
                            </table>
                        </div>
                        <Modal onCancel={() => setVisible(false)} footer={null} visible={visible}>
                            <CategoryForm value={updateName} setValue={setUpdatedName} handleSubmit={handleUpdate} />
                        </Modal>
                    </div>
                </div>
            </div>
        </Layout>
    )
}

export default CreateCategory